.header {
  display: flex;
  //background: red;
  align-items: center;
  flex-direction: row;

  height: 150px;
}

.header__logo-wrapper {
  //flex: 1;
}

.header__logo {
}

.header__logo__title {
  font-family: $font_merriweather;
  font-size: 26px;
  font-weight: 500;
}

.header__nav-wrapper {
  flex: 1;
}

.header__nav {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;

  margin: 0;

  list-style: none;

  li {
    margin-left: 25px;

    @include respond-to('xlarge') {
      margin-left: 95px;
    }
  }

  a {
    position: relative;

    display: block;

    color: $color_black;
    font-size: 13px;
    font-weight: 700;
    line-height: 40px;
    text-decoration: none;

    @include respond-to('large') {
      font-size: 16px;
    }

    &:after {
      content: '';

      position: absolute;
      bottom: 0;
      left: 0;

      display: block;

      width: 100%;
      height: 2px;

      background: $color_blue;

      opacity: 0;
      transition: opacity linear 0.5s;
    }

    &:hover {
      &:after {
        opacity: 1;
      }
    }
  }
}
