.properties {
  @include respond-to('medium') {
    padding-top: 100px;
  }
  @include respond-to('xlarge') {
    padding-top: 285px;
  }

  .section__item--blue {
    h3 {
      color: $color_black;
      strong {
        color: $color_blue;
      }
    }
  }

  .section__item--red {
    h3 {
      color: $color_black;
      strong {
        color: $color_red;
      }
    }
  }
}
